import PathParser from "el/editor/parser/PathParser";
import icon from "el/editor/icon/icon";
import { SVGItem } from "./SVGItem";
import { vec3 } from "gl-matrix";


export class SVGStarItem extends SVGItem {

  getIcon () {
    return icon.star;
  }  
  getDefaultObject(obj = {}) {
    return super.getDefaultObject({
      itemType: 'star',
      name: "New Star",   
      'stroke-width': 1,
      d: '',        //       segments  . 
      isCurve: false,
      count: 5,     // outer radius
      radius: 0.5,  // inner radius rate 
      tension: 0.5,   //  
      ...obj
    });
  }

  convert(json) {
    json = super.convert(json);

    if (json.count) json.count = +(json.count);
    if (json.radius) json.radius = +(json.radius);
    if (json.tension) json.tension = +(json.tension);

    return json;
  }

  enableHasChildren() {
    return false; 
  }

  get d() {
    const {width, height, count, radius, tension, isCurve} = this.json;

    let newPath = "";
    if (isCurve) {
      newPath = PathParser.makeCurvedStar(width.value, height.value, count, radius, tension).d;
    } else {
      newPath = PathParser.makeStar(width.value, height.value, count, radius).d;
    }

    return newPath;
  }
 

  toCloneObject() {
    return {
      ...super.toCloneObject(),
      ...this.attrs('count', 'radius', 'tension', 'isCurve')
    }
  }

  getDefaultTitle() {
    return "Star";
  }

}